.ButtonComponent_btn__1Bp0x{
    border: none;
    display: block;
    position: relative;
    text-decoration: none;
    color: #f9a9ae;
    width: 120px;
    height: 50px;
    line-height: 50px;
    border-radius: 5px;
    text-align: center;
    vertical-align: middle;
    overflow: hidden;
    font-weight: bold;
    font-size: 18px;
    transition: .4s;
    background-image: linear-gradient(#fed6e3 0%, #ffaaaa 100%);
    text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.66);
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.28);
    margin: 10px 20px;
}

.ButtonComponent_btn__1Bp0x:active{
    transform: translateY(2px);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
    background-image: linear-gradient(#fed6e3 0%, #ffbcbc 100%);
}
.ButtonComponent_btn__1Bp0x:hover {
    background-image: linear-gradient(#ffcad9 0%, #ff9494 100%);
    color: #ff858e;
}
.ButtonComponent_btn__1Bp0x:disabled {
    background-image: linear-gradient(rgba(255, 202, 217, 0.29) 0%, rgba(255, 148, 148, 0.37) 100%);
    color: rgba(255, 133, 142, 0.38);
}
input[type=checkbox] {
    -moz-appearance: none;
    -webkit-appearance: none;
    -o-appearance: none;
    outline: none;
    content: none;
}

.CheckboxComponent_checkboxComponent__1Bmom {
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
}

.CheckboxComponent_checkboxComponent__1Bmom {
    content: '';
    background: #fff;
    display: inline-block;
    vertical-align: middle;
    width: 25px;
    height: 25px;
    padding: 2px;
    text-align: center;
    border: 2px solid lightcoral;
    margin: 20px;
    border-radius: 3px;

}

.CheckboxComponent_checkboxComponent__1Bmom:checked {
    background: lightcoral;
